img {
  max-width: 100%; }

*,
::after,
::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

.container {
  margin: 0 auto; }
  @media screen and (max-width: 1023px) {
    .container {
      width: 100%;
      padding: 0 20px; } }
  @media screen and (max-width: 767px) {
    .container {
      padding: 0 14px; } }

.small-container {
  width: 1173px;
  margin: 0 auto; }
  @media screen and (max-width: 1460px) {
    .small-container {
      width: 100%; } }
  @media screen and (max-width: 1023px) {
    .small-container {
      width: 100%;
      padding: 0 20px; } }
  @media screen and (max-width: 767px) {
    .small-container {
      padding: 0 14px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%; }

.page body {
  margin: 0;
  min-height: 100%;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  -ms-flex-line-pack: start;
      align-content: start; }

.page main {
  overflow: hidden; }
